///////////////////////////////////////////////////////////////////////// 마이페이지 LNB
.lnb {
    width:220px;
    display:inline-block;
    h3 {
        position: relative;
        font-size:26px;
        font-weight:bold;
        color:#b14248;
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 5px solid;
        border-image: linear-gradient(to right, #b14248 0%, #bd6d33 100%);
        border-image-slice: 1;
        &::after {
            content:'';
            position: absolute;
            top: 15px;
            right: 0;
            width: 10px;
            border-radius: 50%;
            height: 10px;
            background: #bd6d33;
        }
    }
    .lnb-menu {
        > li {
            width:100%;
            border-bottom:1px solid #eaebec;
            > a {
                font-size:15px;
                color:#000;
                display:block;
                padding:20px 0;
                &.active {
                    color:#b14248;
                }
            }
            .lnb-depth03 {
                display:none;
                padding:10px;
                li {
                    margin-bottom:15px;
                    a {
                        display:block;
                        position:relative;
                        padding:0 25px;
                        font-size:13px;
                        color:#666666;
                        &.active, &:hover {
                            color:#b14248;
                        }
                    }
                }
            }
            &.active {
                > a {
                    color:#b14248;
                    font-weight:bold;
                    border:none;background:url("../images/lnb_plus.png") no-repeat right center;
                }
            }
        }
    }
}


///////////////////////////////////////////////////////////////////////// 마이페이지 탭
input[type="radio"] {display:none;}
.tabs {
    display:flex;
    overflow:hidden;
    .tab {
        width:25%;
        padding: 20px;
        display:inline-block;
        font-size:16px;
        color:#666666;
        text-align:center;
        border:1px solid #eaebec;
        cursor: pointer;
        span {
            font-size:20px;
            font-weight:bold;
            color:#b94a38;
            margin-left:10px;
        }
    }
}

.panel{display:none;padding-top:20px;animation: fadein .8s;}
@keyframes fadein {
    from {
        opacity:0;
    }
    to {
      opacity:1;
  }
}
#one:checked ~ .panels #one-panel,
#two:checked ~ .panels #two-panel,
#three:checked ~ .panels #three-panel,
#four:checked ~ .panels #four-panel {display:block}
#one:checked ~ .tabs #one-tab,
#two:checked ~ .tabs #two-tab,
#three:checked ~ .tabs #three-tab,
#four:checked ~ .tabs #four-tab {background:#b94a38;color:#fff;border:1px solid #b94a38;}
#one:checked ~ .tabs #one-tab span,
#two:checked ~ .tabs #two-tab span,
#three:checked ~ .tabs #three-tab span,
#four:checked ~ .tabs #four-tab span {color:#fff;}

